* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
button {
  outline: none;
}
a:focus-visible,
button:focus-visible {
  outline: 3px solid #E89F71;
  outline-offset: 4px;
}

a {
  color: inherit;
  outline: none;
}

input:focus {
  box-shadow: inset 0 0 5px 2px #E89F71;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  line-height: 1;
  font-family: "Gilroy", sans-serif;
  color: #3A3A3A;
  font-weight: 500;
  font-style: normal;
}

input,
button,
textarea {
  font-size: inherit;
  font-family: "Gilroy", sans-serif;
}

button {
  cursor: pointer;
  background-color: transparent;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.wrapper {
  width: 100%;
  min-height: auto;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: all 0.3s ease 0s;
}
.wrapper.blur {
  background-color: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(8px);
  transition: all 0.3s ease 0s;
}

.main {
  flex: 1 1 auto;
}
.main__home {
  padding-top: 133px;
}
@media (min-width: 1600px) {
  .main__home {
    padding-top: 200px;
  }
}
@media (max-height: 650px) {
  .main__home {
    padding-top: 150px;
  }
}
@media (max-width: 992px) {
  .main__home {
    padding-top: 90px;
    height: max-content;
  }
}
@media (max-width: 480px) {
  .main__home {
    padding-top: 70px;
  }
}
.main__benefits {
  padding: 88px 0;
}
@media (max-width: 769px) {
  .main__benefits {
    padding: 25px 0;
  }
}
.main__products {
  padding-bottom: 44px;
}
@media (max-width: 769px) {
  .main__products {
    padding-bottom: 40px;
  }
}
.main__rooms {
  padding: 44px 0;
}
@media (max-width: 769px) {
  .main__rooms {
    padding: 20px 0;
  }
}
.main__tips {
  padding: 44px 0;
}
@media (max-width: 769px) {
  .main__tips {
    padding: 20px 0;
  }
}
.main__furniture {
  padding-bottom: 87px;
}
@media (max-width: 992px) {
  .main__furniture {
    padding-bottom: 60px;
  }
}
@media (max-width: 769px) {
  .main__furniture {
    padding-bottom: 30px;
  }
}

.container {
  max-width: 1270px;
  padding: 0 15px;
  margin: 0 auto;
}
@media (max-width: 769px) {
  .container {
    padding: 0 10px;
  }
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  padding: 0 10px;
}

.btn-orange {
  color: #fff;
  background-color: #E89F71;
  transition: background-color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .btn-orange:hover {
    background-color: #cc875c;
  }
}

.btn-white {
  color: #E89F71;
  background-color: #fff;
  transition: background-color 0.3s ease, color 0.3s ease;
}
@media (any-hover: hover) {
  .btn-white:hover {
    background-color: #E89F71;
    color: #fff;
  }
}

.checkbox {
  appearance: none;
}
.checkbox:checked + .checkbox-tag::after {
  transform: scale(1);
}
.checkbox:focus-visible + .checkbox-tag {
  outline: 2px solid #E89F71;
  outline-offset: 3px;
}
.checkbox input::-webkit-outer-spin-button,
.checkbox input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.checkbox-tag {
  position: relative;
  display: inline-block;
}
.checkbox-tag::before {
  content: "";
  position: absolute;
  top: -2px;
  left: 0;
  width: 15px;
  height: 15px;
  border: 2px solid #E89F71;
}
.checkbox-tag::after {
  transition: all 0.3 ease 0s;
  position: absolute;
  content: "";
  top: 1px;
  left: 3px;
  width: 9px;
  height: 9px;
  background-color: #E89F71;
  transform: scale(0);
}

.title {
  font-weight: 700;
  font-size: 40px;
}
@media (max-width: 480px) {
  .title {
    font-size: 35px;
  }
}

.footer-title {
  font-weight: 700;
  font-size: 20px;
  line-height: 29px;
  color: #000;
}

.arrow {
  width: 48px;
  height: 48px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: auto;
  transition: all 0.3s ease 0s;
}
@media (any-hover: hover) {
  .arrow:hover {
    background-color: #cc875c;
    color: #fff;
  }
}
@media (max-width: 769px) {
  .arrow {
    width: 38px;
    height: 38px;
  }
}
@media (max-width: 480px) {
  .arrow {
    width: 28px;
    height: 28px;
  }
}

.arrow-prev {
  transform: rotate(-180deg);
}

.arrow-white {
  background-color: #fff;
  color: #E89F71;
  box-shadow: 0px 4px 14px 1px rgba(0, 0, 0, 0.16);
}

.flyImage {
  opacity: 1;
  z-index: 100;
  position: fixed;
  transition: all 1.5s ease 0s;
}

.product-btn.hold {
  opacity: 0.2;
  cursor: default;
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #F9F1E7;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease 0s;
  opacity: 1;
  visibility: visible;
}
.preloader.done {
  opacity: 0;
  visibility: hidden;
}

