.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 500;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}
.header.active {
  background-color: rgba(255, 255, 255, 0.85);
  box-shadow: 0 0 10px 10px rgba(0, 0, 0, 0.2);
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}
.header.active .header__body {
  min-height: 90px;
}
@media (max-width: 480px) {
  .header.active .header__body {
    min-height: 50px;
  }
  .header.active .header__body .header__search.active {
    transform: translateY(130%);
  }
}
.header__body {
  position: relative;
  min-height: 125px;
  padding: 10px 10px;
  display: flex;
  align-items: center;
  will-change: min-height;
  transition: min-height 0.3 ease;
}
@media (max-width: 992px) {
  .header__body {
    min-height: 90px;
  }
}
@media (max-width: 769px) {
  .header__body {
    padding: 10px;
  }
}
@media (max-width: 480px) {
  .header__body {
    min-height: 70px;
  }
}
.header__logo {
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  color: #000;
  margin-right: 41px;
}
@media (min-width: 769px) {
  .header__menu {
    flex: 0 0 336px;
    margin-right: 43px;
  }
}
@media (max-width: 1200px) {
  .header__menu {
    margin-right: 20px;
  }
}
.header__search {
  height: 45px;
  flex: 0 1 473px;
  margin-right: 20px;
}
@media (max-width: 992px) {
  .header__search {
    position: absolute;
    top: 20;
    left: 0;
    width: 100%;
    z-index: -1;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-100%);
    transition: opacity 0.5s ease 0.3s, visibility 0.5s ease 0.3s, transform 0.5s ease 0s;
  }
  .header__search.active {
    opacity: 1;
    visibility: visible;
    transform: translateY(200%);
    transition: opacity 0.5s ease 0s, visibility 0.5s ease 0s, transform 0.5s ease 0s;
    box-shadow: 0 0 10px 10px rgba(0, 0, 0, 0.2);
  }
}
@media (max-width: 992px) and (max-width: 992px) {
  .header__search.active {
    transform: translateY(150%);
  }
}
@media (max-width: 992px) and (max-width: 480px) {
  .header__search.active {
    transform: translateY(128%);
  }
}
.header__actions {
  margin-left: auto;
}

@media (max-width: 769px) {
  .menu-header__body {
