    z-index: 1000;
    overflow: auto;
    background-color: #e7f7f9;
    padding: 150px 25px;
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.7);
    transition: transform 0.5s ease 0s;
    text-align: center;
  }
  .menu-header__body.active {
    transform: translateX(0);
  }
}
.menu-header__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media (max-width: 769px) {
  .menu-header__list {
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: flex-start;
  }
}
.menu-header__item:not(:last-child) {
  margin: 0 40px 0 0;
}
@media (max-width: 769px) {
  .menu-header__item {
    width: 100%;
  }
  .menu-header__item:not(:last-child) {
    margin: 0 0 25px 0;
  }
}
.menu-header__item[data-header-item] {
