.menu-header__btn.active .menu-header__arrow {
  transform: rotate(-180deg);
}
.menu-header__btn.active + .menu-header__sublist {
  pointer-events: auto;
  opacity: 1;
  visibility: visible;
}
.menu-header__arrow {
  display: block;
  font-size: 10px;
  margin-left: 8px;
